'use strict';

define('app/base/view',[
    'backbone', 
    'app/base/viewManager'
], function(
    Backbone,
    ViewManager){

    var View;

    return (View = Backbone.View.extend({
        className: 'row',
        initialize: function(){
            View.__super__.initialize.apply(this, arguments);  
        },
        render: function(context, template){
            return this._render(context, template);
        },
        serialize: function(){
            var $form;
            if(this.$el.is('form')){
                $form = this.$el;
            } else {
                $form = this.$('form');
            }
            return $form.serializeObject(); 
        },
        _render: function(context, template){
            var html = '', hasError = false;
            template = template || (context && context.template) || '';
            if(context && context.template){
                delete context.template;
            }
            try {   
                html = ViewManager.renderTemplate(template, context);
            } catch (ex){
                hasError = true;
            }
            if(!hasError){
                this.$el.html(html);
            }
            return this;
        },
        close: function(){
            View.__super__.remove.apply(this);
            ViewManager.trigger('view:remove', this);
        },
        remove: function(){
            View.__super__.remove.apply(this);
            ViewManager.trigger('view:remove', this);
        }
    }));
});
