'use strict';

define('app/app',[
    'backbone',
    'app/router',
    'auth/auth'
], function(Backbone, AppRouter, auth){

    var app;

    return {
        init: init
    };

    function init(){
        if(app){
            return app;
        }
        app = new AppRouter();
        auth.init();
        Backbone.history.start();
        //Backbone.history.start({pushState: true});
    }
});
