'use strict';

define('app/router',['backbone'], function(Backbone){

    return Backbone.Router.extend({
        routes: {
            '': 'home'
        },
        home: function(){
            this.render('<p>Home</p>');
        },
        render: function(template){
            $('#shell').html(template);
        }
    });
});
