'use strict';

define('auth/router',[
    'app/base/router', 
    'app/base/viewManager',
    'auth/views/loginView',
    'auth/views/registerView'
], function(
    BaseRouter, 
    ViewManger,
    LoginView,
    RegisterView){

    return BaseRouter.extend({
        routes: {
            'login': 'login',
            'register': 'register'
        },
        login: function(){
            var loginView = new LoginView();
            this.listenTo(loginView, 'login', this.login_);
            ViewManger.renderView(loginView);
        },
        login_: function(data){
            console.log('login.data', data);
            //this.navigate('', true);
        },
        register: function(){
            var registerView = new RegisterView();
            this.listenTo(registerView, 'register', this.register_);
            ViewManger.renderView(registerView);
        },
        register_: function(data){
            console.log('data', data);
        }
    });
});
