'use strict';

define('app/base/viewManager',['backbone'], function(Backbone){

    var mapCompiled = {};
    var instance;
    var viewInstance;

    function ViewManager(){
        this.initialize.apply(this, arguments);
    }

    _.extend(ViewManager.prototype, Backbone.Events, {
        initialize: function(){
            this.events();
        },
        events: function(){
            this.on('view:remove', this.removeView);
        },
        renderView: function(view){
            var args = _(arguments).slice(1);
            if(viewInstance){
                viewInstance.close();
            }
            viewInstance = view;
            $('#shell').html(view.render.apply(view, args).el);
        },
        renderTemplate: function(template, context){
            var compiled = this._getCompiled(template);
            return compiled(context);
        },
        _getCompiled: function(template){
            return mapCompiled[template] || (mapCompiled[template] = _.template(template || ''));
        },
        removeView: function(view){
            view = null;
        }
    });

    instance = new ViewManager();

    return instance;
});
